<template>

  <el-row style="height: 100%" >
      <el-row  style="height: 14%;padding-top: 20px;text-align: center;" :gutter="20" >
        <div style="text-align: center;margin: 0 auto; height: 60%;width: 100%">
              <el-input v-model="title" placeholder="请输入内容" style="height: 100%;display: inline-block;width: 250px"></el-input>
<!--              <el-input class="input" list></el-input>-->
              <el-select filterable allow-create v-model="category" placeholder="请选择"  style="height: 100%;display: inline-block;width: 200px">
                <el-option
                  v-for="item in options"
                  :key="item"
                  :label="item"
                  :value="item">
                </el-option>
              </el-select>
              <el-button type="primary" @click="submit" style="line-height: 0px;height: 40px;width: 100px;display: inline-block">提交</el-button>
              <el-button id="del" type="danger" @click="delArticle" style="line-height: 0px;height: 40px;width: 120px;" :class="{show: editOldArticle}">删除这篇文章</el-button>
              <a id="LookBtn" style="" :class="{showNewArticle: submitSuccess}" @click="LookNewArticle">查看新文章: {{title}}</a>
        </div>
      </el-row>
      <el-row style="height: 2%">
        <el-divider></el-divider>
      </el-row>
      <el-row style="height: 88%">
        <mavon-editor ref=md @imgAdd="$imgAdd" @imgDel="$imgDel" v-model="content" :toolbars="toolbars" :imageFilter = "uploadBefore" @keydown="" style="height: 100%"/>
      </el-row>
  </el-row>


</template>

<script>
import Axios from 'axios'
import axios from "axios"
import Vue from 'vue'
let showdown  = require('showdown')
let Base64 = require('js-base64')
import 'mavon-editor/dist/css/index.css'
import editor from 'mavon-editor'
import showdownKatex from "showdown-katex";
// use
Vue.use(editor)
Vue.prototype.$axios=Axios
// 设置基本路径
Axios.defaults.baseURL='/api'


export default {
  name: "WriteContent",

  data() {
    return {
      options: '',
      category: '',
      title: '',
      content: '',
      old_category: '',
      old_title: '',
      old_content: '',
      textarea2: '',
      img_file: [[0, null]],
      editOldArticle: false,
      submitSuccess: false,
      fromPath: '',
      toolbars: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        mark: true, // 标记
        superscript: true, // 上角标
        quote: true, // 引用
        ol: true, // 有序列表
        link: true, // 链接
        imagelink: true, // 图片链接
        help: true, // 帮助
        code: true, // code
        subfield: true, // 是否需要分栏
        fullscreen: true, // 全屏编辑
        readmodel: true, // 沉浸式阅读
        /* 1.3.5 */
        undo: true, // 上一步
        trash: true, // 清空
        save: true, // 保存（触发events中的save事件）
        /* 1.4.2 */
        navigation: true, // 导航目录
      },

    }
  },
  // beforeRouteEnter(to, from, next) {
  //   next(vm => {
  //     // 通过 `vm` 访问组件实例,将值传入fromPath
  //     vm.fromPath = from.path
  //   })
  // },
  created() {
    this.old_category = this.$route.params.category
    this.old_title = this.$route.params.title
    this.old_content = this.$route.params.content
    this.category = this.$route.params.category
    this.title = this.$route.params.title
    this.content = this.$route.params.content
    this.$axios.get('/myBlog/queryCategory').then(res => {
      this.options = res.data
    }).catch(error => {
      console.log(error);
    })
  },
  methods: {

    $imgAdd(pos, $file) {

      let formData = new FormData()
      formData.append('file', $file)
      // this.$serve.upload(formdata).then((res) => {
      //   if (res.errCode === '0') {
      //     this.$refs.mavonEditor.$img2Url(pos, this.baseUrl + '/' + res.imgUrl)
      //   }
      // })
      this.$axios.post('/myBlog/uploadImg', formData).then(res=>{
        if(res.status === 200) {
          const url = res.data
          console.log("img_url", url, pos);
          this.$refs.md.$img2Url(pos, url)
        }
      }).then((response) => {
        // 第二步.将返回的url替换到文本原位置![...](0) -> ![...](url)
        // if (response.status === 200) {
        console.log("hello world");
        console.log(response);
        // var url = response.data.data;
        // console.log("hello world");
        // _this.$refs.md.$img2Url(pos,url)
        // }
      }).catch(err => {
        console.log('failed')
      })
    },
    $imgDel($file, name) {
      let imgName = encodeURI(name)
      this.$axios.get('/myBlog/delImg?imgName='+imgName).then(res => {
        // console.log(res.data)
      }).catch(err => {
        console.log('failed');
      })
    },
    submit() {
      // console.log("编辑or插入"+this.editOldArticle)
      if(this.editOldArticle) {
        if(this.title === '' || this.old_title ==='' || this.category === '' || this.old_category === '' || this.content === '' || this.old_content === '') {
          this.$alert('标题或类别内容不能为空', '提交失败', {
            confirmButtonText: '确定',
          });
          return
        }
        let data = new FormData();
        let articleInfo = {
          category: this.category,
          title: this.title,
          content: this.content,
          old_category: this.old_category,
          old_title: this.old_title,
          old_content: this.old_content
        };
        data.append("articleInfo", JSON.stringify(articleInfo));
        this.$axios.post('/myBlog/updateContent', data).then(res => {
          // console.log("更新成功："+res.data)
          if(res.data) {
            const h = this.$createElement;
            this.$notify({
              title: '标题名称',
              message: h('i', { style: 'color: green'}, '更新成功!')
            });
            this.submitSuccess = true
          }
        }).catch(error => {
          console.log(error);
        })
      } else {
        // console.log("插入新书");
        let data = new FormData();
        let articleInfo = {
          category: this.category,
          title: this.title,
          content: this.content,
        };
        data.append("articleInfo", JSON.stringify(articleInfo));
        this.$axios.post('/myBlog/addNewArticle', data).then(res => {
          if(res.data) {
            const h = this.$createElement;
            this.$notify({
              title: '标题名称',
              message: h('i', { style: 'color: green'}, '添加成功!')
            });
            this.submitSuccess = true
          }
        }).catch(error => {
          const h = this.$createElement;
          this.$notify({
            title: '标题名称',
            message: h('i', { style: 'color: red'}, '添加失败!')
          });
          this.submitSuccess = true
        })
      }
    },
    delArticle() {
      let data = new FormData();
      let articleInfo = {
        category: this.old_category,
        title: this.old_title,
      };
      data.append("articleInfo", JSON.stringify(articleInfo));
      this.$axios.post('/myBlog/delArticle', data).then(res => {
        // console.log(res.data);
        if(res.data) {
          const h = this.$createElement;
          this.$notify({
            title: '标题名称',
            message: h('i', { style: 'color: teal'}, '删除成功!')
          });
          this.$router.replace({
            name: 'main'
          })
        }
      }).catch(error => {
        console.log(error);
      })
    },
    LookNewArticle() {
      this.$router.replace({
        name: 'main',
        params: {
          category: this.category,
          title: this.title,
        }
      })
    },
    // valuechange() {
    //   let converter = new showdown.Converter()
    //   this.rightContent = converter.makeHtml(this.content);
    // }
  },

  beforeRouteEnter(to, from, next) {
    next(vm => {
      if(from.path.indexOf("index") !== -1 && typeof to.params.editOldArticle !== "undefined") {
        vm.editOldArticle = to.params.editOldArticle
      }
    })
  }
}
</script>

<style scoped>

* {
  margin: 0;
  padding: 0;
}
body, html{
  margin: 0;
  padding: 0;
}

#del {
  display: none;
}

.show {
  display: inline-block !important;
}
#LookBtn {
  display: none;
  color: dodgerblue;
  cursor: pointer;
}
#LookBtn:hover {
  color: red;

}
.showNewArticle {
  display: block !important;
}


</style>



