<template>
  <div style="height: 100%;" >
    <div id="sidebar-left-scroll"
         style="display: inline-block;width: 130px;height: 100%;overflow-y: scroll;overflow-x: hidden;box-sizing:border-box;float: left;color: white;">
      <div style="height: 180px;width:100%;text-align: center;padding-top: 10px;">
        <div id="img_head"></div>
        <!--        <el-avatar src= :size="120" shape="circle" style="opacity: 1;background-color: black"></el-avatar>-->
        <div style="font-size: 23px;margin-top: 5px;">
          <a><i class="icon-info el-icon-phone"></i></a>
          <a><i class="icon-info el-icon-s-custom"></i></a>
          <a><i class="icon-info el-icon-chat-line-round"></i></a>
        </div>
      </div>
      <div class="sidebar_left"
           style="height: 50px;text-align: center;border-top: 1px solid white;line-height: 50px; cursor: pointer;opacity: 0.5"
           v-for="(item, index) in article_category" :class="{categoryActive: item === current_category}">
        <li style="height: 45px;" @click="queryTitle(item, index)">{{ item }}</li>
      </div>
    </div>
    <div id="sidebar-right-scroll"
         style="display: inline-block;width: 270px;height: 100%;overflow: scroll;box-sizing:border-box;float: left;opacity: 1;background-color: rgba(255, 255,255,0.2);">
      <div style="height: 30px;">
        <el-input type="text" v-model="searchContent" placeholder="请输入关键字" style="width:100%;"
                  @keydown.enter.native="searchTitle"></el-input>
      </div>
      <div style="margin-top: 16px">
        <hr/>
      </div>
      <li v-for="item in article_title" @click="queryContent(item)" :class="{titleActive: item === current_title}" class="titleItem"
          style="height: auto;width:auto;text-align: left;margin:0; border-bottom: 1px solid lightgrey;line-height: 27px;font-size: 10px;overflow:visible; white-space: nowrap;cursor: pointer">
        <i class="el-icon-arrow-right" style="display: inline"></i>
        {{ item }}
      </li>
    </div>
    <div id="contentBox"
      style="display: inline-block;width:calc(100% - 400px);height: 100%;box-sizing:border-box;opacity: 1;background-color: rgba(255, 255,255,0.8)">
      <el-row style="width: 100%;height: 10%;padding: 15px;">
        <div style="text-align: center">
          <el-link id="titleName" type="primary" style="font-size: 20px" @click="editArticle">{{ current_title }}
          </el-link>
        </div>
<!--        <a @click="drawer = true"><i class="el-icon-menu" style="float: right"></i></a>-->
      </el-row>
      <el-row style="height: 90%">
        <div id="content"
             style="height: 100%; overflow-y: scroll;box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);">
          <div id="articleContent" style="" v-html=""></div>
        </div>
      </el-row>
    </div>

    <button id="writeBtn" style="font-size: 16px;position: fixed; right: 5px;top: 10px;color: blue;z-index: auto;" @click="writeEvent"><i class="el-icon-edit" style="">write</i></button>
<!--/*      <a href="" style="position: absolute; right: 45px;top: 10px"><<<</a>*/-->
<!--/*      <div style="display: block;position: absolute;top: 10px;right: 0px"><button style="background-color: rgba(0,0,0,0);border: none;font-size: 16px;line-height: 30px">写博客</button></div>*/-->

  </div>
</template>

<script>


import Axios from 'axios'
import Vue from 'vue'
const head_img = require('../assets/img/head_img.jpg')
let showdown = require('showdown')
let Base64 = require('js-base64')

var markdown_config = {
  html: true,        // Enable HTML tags in source
  xhtmlOut: true,        // Use '/' to close single tags (<br />).
  breaks: true,        // Convert '\n' in paragraphs into <br>
  langPrefix: 'lang-',  // CSS language prefix for fenced blocks. Can be
  linkify: true,        // 自动识别url
  typographer: true,
  quotes: '“”‘’'
}
let md = require('markdown-it')(markdown_config)
let katex = require('markdown-it-katex')
// let katex = require('markdown-it-katex-external')
// let katex = require('markdown-it-latex2img')
// let katex = require('katex')

let emoji = require('markdown-it-emoji');
// 下标
let sub = require('markdown-it-sub')
// 上标
let sup = require('markdown-it-sup')
// <dl/>
let deflist = require('markdown-it-deflist')
// <abbr/>
let abbr = require('markdown-it-abbr')
// footnote
let footnote = require('markdown-it-footnote')
// insert 带有下划线 样式 ++ ++
let insert = require('markdown-it-ins')
// mark
let mark = require('markdown-it-mark')
// taskLists
let taskLists = require('markdown-it-task-lists')
// container
let container = require('markdown-it-container')
//
let mip = require('markdown-it-images-preview');
let toc = require('markdown-it-toc')
let tm = require('markdown-it-texmath');
md.use(require("markdown-it-disable-url-encode"), "*")
md.use(emoji)
  .use(sup)
  .use(sub)
  .use(deflist)
  .use(abbr)
  .use(footnote)
  .use(insert)
  .use(mark)
  .use(container)
  .use(taskLists)
  .use(toc)
  .use(mip)

  .use(tm, {
    engine: require('katex'),
    delimiters: 'dollars',
    katexOptions: {macros: {"\\RR": "\\mathbb{R}"}}
  });
// md.use(plugin);

// Vue.prototype.$axios = Axios
// 设置基本路径
Axios.defaults.baseURL = '/api'

export default {
  name: "SideBar",
  data() {
    return {
      context: ' ',//输入的数据
      article_category: ['首页', '全部文章'],
      article_title: [],
      article_url: '',
      article_content: 'welcome!',
      current_category: '首页',
      current_title: '',
      ori_content: '',
      searchContent: '',
      currentIndex: 0,
      // activeColor: 'lightgray',
      category_title: [],
      //抽屉数据

    }
  },
  created() {
    console.log("创建成功");
  },
  mounted() {
    head_img
    console.log("挂载成功");
    this.queryAllCategory()
    console.log("查询成功");
    document.getElementById("articleContent").innerHTML = "欢迎来到我的博客!"
    // this.queryTitle('全部文章', 0)
    this.getAllTitle()
  },
  beforeRouteEnter(to, from, next) {
    console.log("路由守卫");
    next(vm => {          //  这里的vm指的就是vue实例，可以用来当做this使用
      if (from.path.indexOf("/write") !== -1) {
        if (typeof to.params.category !== "undefined") {
          vm.current_category = to.params.category
          vm.current_title = to.params.title
          vm.queryTitle(vm.current_category, 0)
          vm.queryContent(vm.current_title)
        }
      }
    })
  },
  computed: {
    // activeStyle(index) {
    //   return index === this.currentIndex ? {color: this.activeColor} : {color: "#6272a4"}
    // }
  },
  methods: {
    queryAllCategory() {
      this.$axios.get('/myBlog/queryCategory').then(res => {
        // this.article_category = res.data
        if (res.data != null) {
          for (let i = 0; i < res.data.length; i++) {
            this.article_category.push(res.data[i])
          }
        }
      }).catch(error => {
        console.log(error);
      })
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    queryTitle(event, index) {
      this.current_category = event
      if (event === "首页") {
        document.getElementById("articleContent").innerHTML = "欢迎来到我的博客!"
        this.article_title = ''
        return
      }
      let category_base64 = Base64.encode(event)
      if (event !== "全部文章") {
        this.$axios.get('/myBlog/queryTitle?category=' + category_base64).then(res => {
          this.article_title = res.data
        }).catch(error => {
          console.log(error);
        })
      } else {
        //得到所有的 <title, category>数据组
        this.getAllTitle()
        //展示所有title
        this.article_title = Object.keys(this.category_title)
      }
    },
    getAllTitle() {
      this.$axios.get('/myBlog/getAllCategoryAndTitle').then(res => {
        // this.article_title = Object.keys(res.data)
        this.category_title = res.data
      }).catch(error => {
        console.log(error);
      })
    },
    searchTitle() {
      let title_list = []
      for (let i = 0; i < Object.keys(this.category_title).length; i++) {
        let tmp_title = Object.keys(this.category_title)[i]
        if (tmp_title.indexOf(this.searchContent) !== -1) {
          title_list.push(tmp_title)
        }
      }
      this.article_title = title_list
    },
    queryContent(event) {
      if (typeof this.category_title[event] !== "undefined") {
        this.current_category = this.category_title[event];
      }
      this.current_title = event
      let title_base64 = Base64.encode(event)
      let category_base64 = Base64.encode(this.current_category)
      let category_new = category_base64.replaceAll('+', '-')
      let title_new = title_base64.replaceAll('+', '-')
      this.$axios.get('/myBlog/queryContent?category=' + category_new + '&title=' + title_new).then(res => {
        this.ori_content = res.data
        // let img = "![dog.jpg](http://localhost:8088/static/uploads/dog854.jpg)"
        document.getElementById("articleContent").innerHTML = md.render(res.data)
      }).catch(error => {
        console.log(error);
      })
    },
    editArticle() {
      // this.$EventBus.$emit('sendMsg',"这是组件A发送的消息！")
      // history.pushState(this.current_title, this.current_category, '/myBlog/write')
      if (document.cookie.indexOf("username") === -1) {
        this.$router.push({
          name: 'login',
        })
      } else {
        this.$router.push({
          name: 'write',
          params: {
            category: this.current_category,
            title: this.current_title,
            content: this.ori_content,
            newContent: this.article_content,
            editOldArticle: true
          }
        })
      }

    },
    updateStyle () {
      const srs = document.getElementById('contentBox')
      srs.style.width = 'calc(100% - 600px)'
    },
    // updateLocation(event) {
    //   console.log(event.offsetX);
    //   console.log(event.offsetY);
    // }
    writeEvent () {
      this.$router.push({
        name: 'write',
        params: {
          // category: this.current_category,
          // title: this.current_title,
          // content: this.ori_content,
          // newContent: this.article_content,
          // editOldArticle: false
        }
      })
    }
  },
  watch: {},
  updated() {
  }
}

</script>

<style lang="scss">
#img_head {
  width:110px;
  height:110px;
  border-radius: 50%;
  background: url('../assets/img/head_img.jpg') no-repeat center;
  background-size: 100%;
  margin-left: auto;
  margin-right: auto;
}



body, html {
  height: 100%;
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

* {
  margin: 0;
  padding: 0;
}

#sidebar-left-scroll::-webkit-scrollbar-track {

  background-color: lightgray;
  border-radius: 5em;

}

#sidebar-left-scroll::-webkit-scrollbar { /*滚动条整体样式*/
  width: 2px; /*高宽分别对应横竖滚动条的尺寸*/
  height: 1px;
}

#sidebar-left-scroll::-webkit-scrollbar-thumb {

  border-radius: 3px;
  height: 30px; /* 滚动条滑块长度 */
  background-color: green;
}

#content::-webkit-scrollbar { /*滚动条整体样式*/
  width: 2px; /*高宽分别对应横竖滚动条的尺寸*/
  height: 1px;
}

#content::-webkit-scrollbar-thumb {

  border-radius: 3px;
  height: 30px; /* 滚动条滑块长度 */
  background-color: black;
}

#sidebar-right-scroll::-webkit-scrollbar { /*滚动条整体样式*/
  width: 1px; /*高宽分别对应横竖滚动条的尺寸*/
  height: 10px;
}

#sidebar-right-scroll::-webkit-scrollbar-thumb {

  border-radius: 3px;
  height: 30px; /* 滚动条滑块长度 */
  background-color: rgba(255, 255, 255, 0.3);
}

#articleBox::-webkit-scrollbar {
  width: 3px;
}

#articleBox::-webkit-scrollbar-thumb {
  border-radius: 3px;
  height: 30px; /* 滚动条滑块长度 */
  background-color: #6272a4;
}

code {
  display: block;
  background-color: #1e1e1e;
  color: darkgray;
  font-size: 9px;
  margin: 20px;
  padding: 15px;
  font-family: auto;
  line-height: 20px;
  border-radius: 10px;
}

#articleContent p, ul, ol {
  display: block;
  margin: 10px;
  padding: 10px;
  font-size: 14px;
  line-height: 30px;
  //box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);

}

strong {
  font-size: 24px;
}

h1, h2, h3, h4, h5, h6 {
  border-left: 5px solid lightgray;
  padding-left: 5px;
  margin-left: 10px;
}

#articleContent img {
  margin: 10px;
}

#articleContent .katex {

  //background-color: lightgray;
  font-style: normal;
  //font-weight: bold;
}

#articleContent .mathnormal {
  font-style: italic;
}

//.sidebar_left {
//  background-color: dimgray;
//}
.sidebar_left:hover {
  background-color: #6272a4;
}

.categoryActive {
  background-color: #6272a4 !important;
}


.titleItem:hover {
  background-color: #0c93e4;
}
//#7D8B99
.titleActive {
  background-color: #0c93e4 !important;
}

.icon-info {
  cursor: pointer;
}

.icon-info {
  color: lightslategray;
}

.icon-info:hover {
  color: #6272a4;
}

//#sidebar-right-scroll {
//  transform: translateX(-100%);
//}
#writeBtn i:hover {
  color: blue;
  cursor: pointer;
}
</style>



