import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import content from "../components/content";
import WriteContent from "../components/WriteContent";
import SideBar from "../components/SideBar";
import Login from "../components/Login"

Vue.use(Router)

export default new Router({
  mode: "history",
  routes: [
    {
      path: '/',
      name: 'index',
      redirect: '/myBlog/index',
    },
    {
      path: "/myBlog/index",
      name: 'main',
      component: SideBar,
      meta: {
        title: "MyBlog"
      }
    },
    {
      path: '/myBlog/queryArticleInfo',
      name: 'content',
      component: content
    },
    {
      path: '/myBlog/write',
      name: 'write',
      component: WriteContent,
      meta: {
        title: "CreateContent"
      }
    },
    {
      path: "/myBlog/login",
      name: "login",
      component: Login,
      meta: {
        title: "Login"
      }
    },
  ]
})



